
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Badge from 'vux/src/components/badge/index.vue'

import {message} from '../config'
export default {
  components: {
    Group,
    Cell,
    Badge
  },
  data () {
    return {
      list: []
    }
  },
  created () {
    this.$http({
      method: 'jsonp',
      url: message,
      jsonp: 'callback',
      jsonpCallback: 'json',
      params: {
        userId: JSON.parse(this.$localStorage.get('userInfo')).userId,
        limit: 10,
        pageIndex: 0
      }
    })
    .then(res => {
      console.log(res)
      if (res.body.status) {
        this.list = res.body.data.messageList
      } else {
        this.$vux.toast.show({
          type: 'text',
          width: '10em',
          position: 'bottom',
          text: res.body.msg,
          time: '1000'
        })
      }
    })
  }
}



